@media(max-width:768px){
  .card{
    float : none;
    margin : 5%;
    width : 90%;
  }
}
@media(max-width:864px){
  .card{
    float : none;
    margin : 5%;
    width : 90%;
  }
}
@media(max-width:768px){
  .heading{
    width : auto;
    height:auto;
  }
}